function Video(){
  const classes = useStyles();
  let {id,s,e} = useParams();
  const mkv = data[id].seasons[s].episodes[e];
  const [link,setLink] = useState(mkv[0]);
  const [copy,setCopy] = useState('Copy link');
  const handleChange = (event) => {
    setLink(event.target.value);
    setCopy('Copy link');
  };
  return(
    
    <ThemeProvider theme={theme}>
    <ReactPlayer url={link} controls={true} width="100%"/>
    <FormControl variant="filled" className={classes.formControl} color='secondary'>
    <Grid>
    <Select
          value={link}
          onChange={handleChange}
          displayEmpty
          color='secondary'
          input={<BootstrapInput />}
        >
          {mkv.map((v,i) => {
            return <MenuItem value={v} >
              {"Link "+(i+1)}
            </MenuItem>
          })}
        </Select>
        <Button  color='primary' type="button" className={classes.button}
    onClick={(e) => {
      e.preventDefault();
      window.location.href='vlc://'+link;
      }}>open in VLC</Button>
          <CopyToClipboard text={link}
          onCopy={() => setCopy('Copied!')}>
          <Button className={classes.button}>{copy}</Button>
        </CopyToClipboard>
      </Grid>
        </FormControl>
        </ThemeProvider>
    );
}

function App() {
  
  return (

    <Router>
      <Switch>
        <Route exact path="/">
          <Home data={data}/>
        </Route>
        <Route path="/video/:id/:s/:e">
          <Video />
        </Route>
      </Switch>
    </Router>
    
  );
}

export default App;
